.NewTodoItem {
    display: flex;
    flex-direction: column;
    
    & .container {
        padding: var(--extra_small_padding);
        padding-bottom: var(--big_padding);

        border: 1px solid var(--light_color);
        border-radius: 7px;
        outline: none;

        &.active {
            border: 1px solid var(--contrast_color);
        }
        
        & .field {
            position: relative;

            max-height: 250px;
            overflow: auto;

            outline: none;
    
            padding: 0 var(--small_padding);
    
            background-color: transparent;
            color: var(--font_main_color);
        }
    }


    & .btnSend {
        background-color: transparent;
        color: var(--font_main_color);
        border: none;
    }
}