:root {
  --font_main_color: #000000;
  --font_light_color: #666666;
  --font_contrast_color: #ffffff;
  
  --main_color: #f2f2f2;
  --light_color: #dddddd;
  --theme_color: #eeeeee;
  --success_color: #009900;
  --danger_color: #990000;
  --accent_color: #2a5cff;


  --contrast_color: #333333;
  --contrast_hover_color: #2ea8e6;


  --big_padding: 50px;
  --middle_padding: 25px;
  --small_padding: 15px;
  --extra_small_padding: 5px;


  --big_font_size: 20px;
  --middle_font_size: 15px;
  --small_font_size: 10px;
}

.darkTheme {
  --font_main_color: #ffffff;
  --font_light_color: #666666;
  --font_contrast_color: #000000;

  --main_color: #333333;
  --light_color: #666666;
  --theme_color: #999999;

  --contrast_color: #f2f2f2;
  --contrast_hover_color: #e62e7a;
}
