.App {
    display: grid;
    grid-template:
        'header' 45px 
        'nav' 
        'main' 1fr;

    min-width: 100vw;
    min-height: 100vh;

    background-color: var(--main_color);

    & .nav {
        grid-area: nav;
    }

    & .header {
        grid-area: header;

        display: flex;
        justify-content: space-between;
        align-items: center;

        padding: var(--small_padding) var(--middle_padding);
        background-color: var(--light_color);
    }

    & .main {
        grid-area: main;
        padding: var(--middle_padding) var(--big_padding);
    }
}

@media (min-width: 768px) {
    .App {
        grid-template:
            'header header header' 45px 
            'nav    main   main'
            /auto 2fr 2fr;
        
        & .header {
            padding: var(--small_padding) var(--big_padding);
        }
    }
}